function slider({
  container,
  slide,
  nextArrow,
  prevArrow,
  totalCounter,
  currentCounter,
  wrapper,
  field,
}) {
  const prev = document.querySelector(prevArrow),
    next = document.querySelector(nextArrow),
    slider = document.querySelector(container),
    slides = document.querySelectorAll(slide),
    current = document.querySelector(currentCounter),
    total = document.querySelector(totalCounter),
    wrapperInner = document.querySelector(wrapper),
    slidesField = document.querySelector(field),
    width = window.getComputedStyle(wrapperInner).width;
  let slideIndex = 1;
  let offset = 0;

  if (slides.length < 10) {
    total.textContent = `0${slides.length}`;
    current.textContent = `0${slideIndex}`;
  } else {
    total.textContent = slides.length;
    current.textContent = slideIndex;
  }

  slidesField.style.width = 100 * slides.length + "%";
  slidesField.style.display = "flex";
  slidesField.style.transition = "0.5s all";

  wrapperInner.style.overflow = "hidden";
  slides.forEach((slide) => {
    slide.style.width = width;
  });

  slider.style.position = "relative";

  const dots = document.createElement("ol"),
    indecators = [];
  dots.classList.add("carousel-indicators");
  dots.style.cssText = `
position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  margin-right: 15%;
  margin-left: 15%;
  list-style: none;
`;

  slider.append(dots);

  for (let i = 0; i < slides.length; i++) {
    const dot = document.createElement("li");
    dot.setAttribute("data-slide-to", i + 1);
    dot.style.cssText = `
   box-sizing: content-box;
  flex: 0 1 auto;
  width: 30px;
  height: 6px;
  margin-right: 3px;
  margin-left: 3px;
  cursor: pointer;
  background-color: #fff;
  background-clip: padding-box;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  opacity: .5;
  transition: opacity .6s ease;
  `;
    if (i == 0) {
      dot.style.opacity = 1;
    }
    dots.append(dot);
    indecators.push(dot);
  }

  function deleteNotDigit(str) {
    return +str.replace(/\D/g, "");
  }

  function getZeroSlides() {
    if (slides.length < 10) {
      current.textContent = `0${slideIndex}`;
    } else {
      current.textContent = slideIndex;
    }
  }

  function setStyleIndecators() {
    indecators.forEach((indecator) => {
      indecator.style.opacity = ".5";
    });
    indecators[slideIndex - 1].style.opacity = 1;
  }

  next.addEventListener("click", () => {
    if (offset == deleteNotDigit(width) * (slides.length - 1)) {
      offset = 0;
    } else {
      offset += deleteNotDigit(width);
    }
    slidesField.style.transform = `translateX(-${offset}px)`;

    if (slideIndex == slides.length) {
      slideIndex = 1;
    } else {
      slideIndex += 1;
    }

    getZeroSlides();
    setStyleIndecators();
  });

  prev.addEventListener("click", () => {
    if (offset == 0) {
      offset = deleteNotDigit(width) * (slides.length - 1);
    } else {
      offset -= deleteNotDigit(width);
    }

    slidesField.style.transform = `translateX(-${offset}px)`;

    if (slideIndex == 1) {
      slideIndex = slides.length;
    } else {
      slideIndex -= 1;
    }

    getZeroSlides();
    setStyleIndecators();
  });

  indecators.forEach((indecator) => {
    indecator.addEventListener("click", (e) => {
      const sliderTo = e.target.getAttribute("data-slide-to");

      slideIndex = sliderTo;
      offset = deleteNotDigit(width) * (sliderTo - 1);

      slidesField.style.transform = `translateX(-${offset}px)`;

      getZeroSlides();
    });
  });
}

export default slider;
